@mixin after-positions {
  top: $positions-square;
  left: $positions-square;
}

@mixin watch-square {
  width: $watch-square;
  height: $watch-square;
}

@mixin watch-square-hover {
  width: $watch-square-hover;
  height: $watch-square-hover;
}

@mixin elements-square {
  width: $element-square;
  height: $element-square;
}
